<script>
export default {
    data() {
		return {
            jobs: []
		}
	},
    mounted() {
        this.$http.get('https://job-vacancy-demo.firebaseio.com/jobs.json').then((response) => {
            return response.json();
        }).then((data) => {
            var jobsArray = [];

            for(var k in data) {
                data[k].id = k;
                jobsArray.push(data[k]);
            }

            this.jobs = jobsArray;
        });
    }
}
</script>



